import React from "react";
import Modal from "react-modal";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Textarea from "muicss/lib/react/textarea";
import { formatCurrency } from "../../lib";

class UnconfirmedDealNoMoney extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      commentModalOpen: false,
      comment: "",
    };

    this.openCommentModal = this.openCommentModal.bind(this);
    this.closeCommentModal = this.closeCommentModal.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleCommentSubmit = this.handleCommentSubmit.bind(this);
    this.handleConfirm = this.handleConfirm.bind(this);
  }

  async updateRating(deal_rating) {
    this.setState({ modalRatingOpen: false });
    await this.props.updateRating(this.props.deal_id, deal_rating);
    this.updateDealReview(
      "updated",
      this.props.ip_date_reviewed,
      this.props.notes_reviewed
    );
  }

  openCommentModal() {
    this.setState({
      commentModalOpen: true,
    });
  }

  closeCommentModal() {
    this.setState({
      commentModalOpen: false,
    });
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;
    this.setState({
      [name]: value,
    });
  }

  handleCommentSubmit() {
    const { comment } = this.state;
    const {
      deal: { commissionid },
    } = this.props;
    if (!comment) return null;
    this.props.commentOnCommission(commissionid, { comment }, "noMoney");
    this.setState({ commentModalOpen: false });
  }

  async handleConfirm() {
    const {
      deal: { commissionid },
    } = this.props;
    await this.props.confirmCommission(commissionid, "noMoney");
  }

  render() {
    const {
      deal: {
        loan_amount,
        contact,
        // commissionid,
        confirmation_status,
        deal_name,
        deal_id,
      } = {},
    } = this.props;
    const { commentModalOpen, comment } = this.state;
    return (
      <div className="pw-deal" key={deal_id}>
        <div className="panel mui-panel">
          <Row className="panel-heading panel-body">
            <Col md="9" sm="9" xs="9">
              <div className="deal-name">
                <p className="panel-info-value property-title">
                  <b style={{ marginRight: 15 }}>
                    <span>{deal_name}</span>
                  </b>
                  <span className="small-title deal-info-title">
                    ID {deal_id}
                  </span>
                </p>
              </div>
            </Col>
            {confirmation_status === "pending" && (
              <Col md="3" sm="3" xs="3" style={{ textAlign: "right" }}>
                <span className="status-tag yellow-background">pending</span>
              </Col>
            )}
          </Row>
          <Row className="panel-body border-bottom">
            <Col md="3" sm="3" xs="3">
              <div>
                <p className="small-title deal-info-title">Client</p>
                <p>{contact}</p>
              </div>
            </Col>
            <Col md="3" sm="3" xs="3">
              <div>
                <p className="small-title deal-info-title">Loan Amount</p>
                <p>{formatCurrency(loan_amount)}</p>
              </div>
            </Col>
          </Row>
          <Row style={{ height: 40 }}>
            <div className="view-all unconfirmed-btn-container">
              <span
                className="orange-text xs-text"
                onClick={this.openCommentModal}
                style={{ fontWeight: 600 }}
              >
                COMMENT
              </span>
              <span
                className="orange-text xs-text"
                style={{ marginLeft: 20, fontWeight: 600 }}
                onClick={this.handleConfirm}
              >
                CONFIRM
              </span>
            </div>
          </Row>
        </div>
        <Modal
          className="share-calc-modal"
          ariaHideApp={false}
          contentLabel="Modal"
          isOpen={commentModalOpen}
          shouldCloseOnOverlayClick={false}
        >
          <div>
            <Textarea
              label="Comment"
              name="comment"
              value={comment}
              onChange={this.handleInputChange}
              rows="3"
            />
            <div style={{ textAlign: "right" }}>
              <span
                className="orange-text xs-text pointer"
                onClick={this.closeCommentModal}
                style={{ marginRight: 15 }}
              >
                Cancel
              </span>
              <span
                className="orange-text xs-text pointer"
                onClick={this.handleCommentSubmit}
              >
                Submit
              </span>
            </div>
          </div>
        </Modal>
      </div>
    );
  }
}

export default UnconfirmedDealNoMoney;
