import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import qs from "query-string";
import UnconfirmedDealsPage from "./UnconfirmedDealsPage";
import * as dealsActions from "../../actions/dealsActions";
import * as navigationTitleActions from "../../actions/navigationTitleActions";
import * as authenticationActions from "../../actions/authenticationActions";
import * as compsActions from "../../actions/compsActions";

let redirectedOnce = false;

class UnconfirmedDealsContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      property_address: this.props.match.params.property_address
        ? this.props.match.params.property_address
        : "",
      showRedirectModal: false,
    };
    this.navChange = this.navChange.bind(this);
    this.closeRedirectModal = this.closeRedirectModal.bind(this);
    this.logout = this.logout.bind(this);
  }

  componentDidMount() {
    if (
      this.props.comps.previousScrollPos &&
      this.props.comps.previousScrollPos.length
    ) {
      window.scrollTo(
        0,
        this.props.comps.previousScrollPos[
          this.props.comps.previousScrollPos.length - 1
        ]
      );
    }
    this.props.navigationTitleActions.updateNavTitle("Unconfirmed Deals");
    this.props.compsActions.resetScrollPosition();
    this.props.dealsActions.unconfirmedDeals();
    this.props.dealsActions.unconfDealsNoMoney();
    const params = qs.parse(this.props.location.search);
    if (params.redirected === "true" && redirectedOnce) {
      this.setState({ showRedirectModal: true });
    }
    redirectedOnce = true;
  }

  closeRedirectModal() {
    this.setState({ showRedirectModal: false });
  }

  navChange(title) {
    this.props.navigationTitleActions.updateNavTitle(title);
  }

  logout() {
    this.props.authenticationActions.logout(localStorage.getItem("auth_token"));
  }

  render() {
    const {
      unconfirmedDeals,
      unconfirmedLoading,
      unconfDealsNoMoney,
      unconfDealsNoMoneyLoading,
      toast,
    } = this.props;

    return (
      <UnconfirmedDealsPage
        unconfirmedLoading={unconfirmedLoading}
        navChange={this.navChange}
        closeToast={this.props.dealsActions.closeToast}
        toast={toast}
        setPageAndScrollPos={this.props.compsActions.setPageAndScrollPos}
        unconfirmedDeals={unconfirmedDeals}
        unconfDealsNoMoney={unconfDealsNoMoney}
        unconfDealsNoMoneyLoading={unconfDealsNoMoneyLoading}
        commentOnCommission={this.props.dealsActions.commentOnCommission}
        confirmCommission={this.props.dealsActions.confirmCommission}
        showRedirectModal={this.state.showRedirectModal}
        closeRedirectModal={this.closeRedirectModal}
        logout={this.logout}
      />
    );
  }
}

function mapStateToProps(state) {
  const {
    pwDeals: {
      // unconfirmed_deal_overdue,
      unconfirmedDeals: {
        deals: unconfirmedDeals,
        loading: unconfirmedLoading,
      } = {},
      unconfDealsNoMoney: {
        deals: unconfDealsNoMoney,
        loading: unconfDealsNoMoneyLoading,
      } = {},
      toast,
    } = {},
    comps,
  } = state;

  return {
    // unconfirmed_deal_overdue,
    unconfirmedDeals,
    unconfirmedLoading,
    unconfDealsNoMoney,
    unconfDealsNoMoneyLoading,
    toast,
    comps,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    dealsActions: bindActionCreators(dealsActions, dispatch),
    navigationTitleActions: bindActionCreators(
      navigationTitleActions,
      dispatch
    ),
    compsActions: bindActionCreators(compsActions, dispatch),
    authenticationActions: bindActionCreators(authenticationActions, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(UnconfirmedDealsContainer);
