import React from "react";
// import { Navigation } from '../../lib';
import UnconfirmedDeal from "./UnconfirmedDeal";
import UnconfirmedDealNoMoney from "./UnconfirmedDealNoMoney";
import moment from "moment";
import Modal from "react-modal";
import Button from "muicss/lib/react/button";
import Tabs from "muicss/lib/react/tabs";
import Tab from "muicss/lib/react/tab";
import Expire from "../common/Expire";
// import encodeQueryData from "../common/encodeQueryData";

class UnconfirmedDealsPage extends React.Component {
  constructor(props) {
    super(props);
    this.formatDate = this.formatDate.bind(this);
    this.state = {
      suggestions: [],
      pwDeals: [],
      input: "",
      confRequiredView: true,
    };
    this.onChange = this.onChange.bind(this);
  }

  formatDate(date) {
    return moment(new Date(date)).format("l");
  }

  onChange(i, value, tab, ev) {
    this.setState({ confRequiredView: value === "confRequired" });
  }

  render() {
    let successMessage = "";
    if (this.props.toast.show) {
      successMessage = (
        <Expire closeToast={this.props.closeToast} delay={6000}>
          <div className="success-message">
            <p>{this.props.toast.message}</p>
            <Button variant="flat" onClick={this.props.closeToast}>
              {this.props.toast.success ? "Done" : "Dismiss"}
            </Button>
          </div>
        </Expire>
      );
    }
    const {
      unconfirmedDeals = [],
      unconfDealsNoMoney = [],
      unconfDealsNoMoneyLoading,
      navChange,
      unconfirmedLoading,
      commentOnCommission,
      confirmCommission,
      showRedirectModal,
      closeRedirectModal,
      logout,
    } = this.props;
    const { confRequiredView } = this.state;
    const noDeals = (
      <div>
        <div>
          <div className="grey-circle">
            <i className="material-icons">warning</i>
          </div>
          <h2 className="no-results">No Deals</h2>
        </div>
      </div>
    );
    const deals =
      !!unconfirmedDeals.length || unconfirmedLoading
        ? unconfirmedDeals.map((deal) => (
            <UnconfirmedDeal
              key={deal.deal_id}
              navChange={navChange}
              deal={{ ...deal }}
              commentOnCommission={commentOnCommission}
              confirmCommission={confirmCommission}
            />
          ))
        : noDeals;
    const noMoney =
      !!unconfDealsNoMoney.length || unconfDealsNoMoneyLoading
        ? unconfDealsNoMoney.map((deal) => (
            <UnconfirmedDealNoMoney
              key={deal.deal_id}
              navChange={navChange}
              deal={{ ...deal }}
              commentOnCommission={commentOnCommission}
              confirmCommission={confirmCommission}
            />
          ))
        : noDeals;
    const loading = unconfirmedLoading || unconfDealsNoMoneyLoading;

    return (
      <div style={{padding: "50px 0"}}>
        <div className="tab-container">
          <Tabs
            onChange={this.onChange}
            defaultSelectedIndex={0}
            className="tabs"
          >
            <Tab
              value="confRequired"
              label="CONFIRMATION REQUIRED"
              className="tab"
            ></Tab>
            <Tab
              value="confNotRequired"
              label="NO CONFIRMATION REQUIRED"
              className="tab"
            ></Tab>
          </Tabs>
        </div>
        <div>
          {confRequiredView ? (
            <p className="unconfirmed-msg">
              The following deals are waiting your confirmation.
            </p>
          ) : (
            <p className="unconfirmed-msg">
              The following deals are scheduled to close within the next seven
              days. You have no money on these deals. If you feel there is an
              error, please comment ASAP.
            </p>
          )}
          {confRequiredView ? deals : noMoney}
          {successMessage}
        </div>
        <Modal
          className="loading-modal"
          ariaHideApp={false}
          contentLabel="Modal"
          isOpen={loading}
          shouldCloseOnOverlayClick={false}
        >
          <div className={loading ? "centered-spinner" : "hidden"}>
            <img
              src="/images/icons/spin.svg"
              className="loading-spinner"
              alt="loading spinner"
            />
          </div>
        </Modal>
        <Modal
          className="share-calc-modal"
          ariaHideApp={false}
          contentLabel="Modal"
          isOpen={showRedirectModal}
          shouldCloseOnOverlayClick={false}
        >
          <div>
            <p className="redirect-modal-title">Sorry, canâ€™t go there now</p>
            <p className="redirect-modal-msg">
              You must resolve these unconfirmed deals before leaving the page.
            </p>
            <div className="orange-text xs-text redirect-modal-action">
              <span onClick={logout}>Logout</span>
              <span onClick={closeRedirectModal} style={{ marginLeft: 20 }}>
                OK
              </span>
            </div>
          </div>
        </Modal>
      </div>
    );
  }
}
export default UnconfirmedDealsPage;
