import React from "react";
import Modal from "react-modal";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Textarea from "muicss/lib/react/textarea";
import { formatCurrency } from "../../lib";

class UnconfirmedDeal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      commentModalOpen: false,
      comment: "",
    };

    this.openCommentModal = this.openCommentModal.bind(this);
    this.closeCommentModal = this.closeCommentModal.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleCommentSubmit = this.handleCommentSubmit.bind(this);
    this.handleConfirm = this.handleConfirm.bind(this);
  }

  async updateRating(deal_rating) {
    this.setState({ modalRatingOpen: false });
    await this.props.updateRating(this.props.deal_id, deal_rating);
    this.updateDealReview(
        "updated",
        this.props.ip_date_reviewed,
        this.props.notes_reviewed
    );
  }

  openCommentModal() {
    this.setState({
      commentModalOpen: true,
    });
  }

  closeCommentModal() {
    this.setState({
      commentModalOpen: false,
    });
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;
    this.setState({
      [name]: value,
    });
  }

  handleCommentSubmit() {
    const { comment } = this.state;
    const {
      deal: { commissionid },
    } = this.props;
    if (!comment) return null;
    this.props.commentOnCommission(commissionid, { comment });
    this.setState({ commentModalOpen: false });
  }

  async handleConfirm() {
    const {
      deal: { commissionid },
    } = this.props;
    await this.props.confirmCommission(commissionid);
  }

  render() {
    const {
      deal: {
        deal_id,
        // commissionid,
        broker_commission,
        // tip,
        tip_percent,
        tip_amount,
        // confirmation_email_at,
        // broker_id,
        deal_name,
        first_name,
        last_name,
        // email,
        contact,
        loan_amount,
        // expected_closing_date,
        bank_fee,
        fee_amount,
        // third_party_fee_amount,
        third_party_fee_notes,
        confirmation_status,
        broker_tips = [],
        additional_tips = [],
        face_of_deal,
      } = {},
    } = this.props;
    // console.log(this.props.deal);
    const senior_uw = Number(face_of_deal) === 1;
    const { commentModalOpen, comment } = this.state;
    const member_id = JSON.parse(localStorage.getItem("data")).member_id;
    // const showDisclaimer =
    //     broker_commission > 0 &&
    //     !!broker_tips.length && broker_tips.some((tip) => tip.missing_hits > 0);

    return (
        <div className="pw-deal" key={deal_id}>
          <div className="panel mui-panel">
            <Row className="panel-heading panel-body">
              <Col md="3" sm="3" xs="3">
                <div className="deal-name">
                  <p className="panel-info-value property-title">
                    <b style={{ marginRight: 15 }}>
                      <span>{deal_name}</span>
                    </b>
                    <span className="small-title deal-info-title">
                    ID {deal_id}
                  </span>
                  </p>
                </div>
              </Col>
              <Col md="3" sm="3" xs="3" style={{ textAlign: "right" }}>
              <span
                  className={
                    !confirmation_status
                        ? "status-tag red-background"
                        : "status-tag yellow-background"
                  }
              >
                {!confirmation_status ? "unconfirmed" : "pending"}
              </span>
              </Col>
            </Row>
            <Row className="panel-body border-bottom">
              <Col md="2" sm="4" xs="6">
                <div>
                  <p className="small-title deal-info-title">Client</p>
                  <p>{contact}</p>
                </div>
              </Col>
              <Col md="2" sm="4" xs="6">
                <div>
                  <p className="small-title deal-info-title">Loan Amount</p>
                  <p>{formatCurrency(loan_amount)}</p>
                </div>
              </Col>
              { !senior_uw && broker_commission > 0 &&(
                  <Col md="2" sm="4" xs="6">
                    <div>
                      <p className="small-title deal-info-title">Fee</p>
                      <p>{formatCurrency(fee_amount)}</p>
                    </div>
                  </Col>
              )}
              { !senior_uw && parseFloat(bank_fee) > 0 && (
                  <Col md="2" sm="4" xs="6">
                    <div>
                      <p className="small-title deal-info-title">Bank Fee</p>
                      <p>{formatCurrency(bank_fee)}</p>
                    </div>
                  </Col>
              )}
              {broker_commission > 0 && (
                <Col md="2" sm="4" xs="6">
                  {/* If getting a portion, then show it, else hide */}
                  <div>
                    <p className="small-title deal-info-title">My Portion</p>
                    <p>{`${broker_commission}%`}</p>
                  </div>
                </Col>
              )}
              { !senior_uw && third_party_fee_notes && (
                  <Col md="2" sm="4" xs="6">
                    <p className="small-title deal-info-title">3rd Party Notes</p>
                    <p>{third_party_fee_notes}</p>
                  </Col>
              )}
            </Row>
            {!senior_uw && (
              <Row className="panel-body border-bottom">
                <p className="uc-panel-info" style={{"textDecoration": "underline"}}>FOR TIP PUPROSES ONLY</p>
                <Row>
                  <p className="small-title deal-info-title">
                    Senior Underwriter
                  </p>
                  <p>{broker_tips.find(i => i.face_of_deal === 1) ? broker_tips.find(i => i.face_of_deal === 1).full_name : 'No Senior Underwriter'}</p>
                </Row>
              </Row>
            )}
            {additional_tips.length > 0 &&
            <Row className="panel-body border-bottom">
              <p className="uc-panel-info" style={{"textDecoration": "underline"}}>ADDITIONAL TIP INFO</p>
              {additional_tips.map((tip) => {
                const {
                  amount : additional_tips_amount,
                  member_id : additional_tips_member_id,
                  percent_of_gross : additional_tips_percent,
                  hitting = [],
                  broker: {
                    full_name: additional_tips_broker_name
                  },
                  additional_tips_broker_percent = "Need Tip Percent",
                } = tip;
                return (
                  <Row
                    key={additional_tips_member_id}
                    className="border-bottom"
                  >
                    <Col md="3" sm="3" xs="12">
                      <div>
                        <p className="small-title deal-info-title">
                          Recipient
                        </p>
                        <p>{additional_tips_broker_name}</p>
                      </div>
                    </Col>
                    {additional_tips_amount > 0 && (
                      <Col md="3" sm="3" xs="12">
                        <div>
                          <p className="small-title deal-info-title">
                            Total Amount (Of Gross Fee)
                          </p>
                          <p>{formatCurrency(additional_tips_amount)} ({`${additional_tips_percent}%`})</p>
                        </div>
                      </Col>
                    )}
                    <Col md="6" sm="6" xs="12">
                      {hitting.map((hitting_additional_tips, index) => {
                        const {
                          member_id : hitting_broker_id,
                          amount : hitting_amount,
                          portion : hitting_portion,
                          broker: {
                            full_name: hitting_broker,
                          },
                        } = hitting_additional_tips;
                        const alternate = index % 2 ? {background: "#f5f8fb"} : {};
                        return (
                          <Col xs="12" key={hitting_broker_id}>
                            <Col xs="6">
                              <div>
                                {index < 1 && <p className="small-title deal-info-title">
                                  Who's Paying
                                </p>}
                                <p style={alternate}>{hitting_broker}</p>
                              </div>
                            </Col>
                            <Col xs="6">
                              <div>
                                {index < 1 && <p className="small-title deal-info-title">
                                  How Much
                                </p>}
                                <p style={alternate}>{formatCurrency(hitting_amount)} ({`${hitting_portion}%`})</p>
                              </div>
                            </Col>
                          </Col>
                        )
                      })}
                    </Col>
                  </Row>
                );
              })}
            </Row>
            }
            <Row className="panel-body border-bottom">
            {senior_uw && !broker_commission ? (
              <p>
                You as the underwriter will receive the underwriter tip, as per your agreement.
              </p>
            ) : !senior_uw ? (
              <p>
                You as the broker are responsible for the tip to the underwriter, as per your agreement.
              </p>
            ) : ''}
            </Row>
            <Row style={{ height: 40 }}>
              <div className="view-all unconfirmed-btn-container">
              <span
                  className="orange-text xs-text"
                  onClick={this.openCommentModal}
                  style={{ fontWeight: 600 }}
              >
                COMMENT
              </span>
                <span
                    className="orange-text xs-text"
                    style={{ marginLeft: 20, fontWeight: 600 }}
                    onClick={this.handleConfirm}
                >
                CONFIRM
              </span>
              </div>
            </Row>
          </div>
          <Modal
              className="share-calc-modal"
              ariaHideApp={false}
              contentLabel="Modal"
              isOpen={commentModalOpen}
              shouldCloseOnOverlayClick={false}
          >
            <div>
              <Textarea
                  label="Comment"
                  name="comment"
                  value={comment}
                  onChange={this.handleInputChange}
                  rows="3"
              />
              <div style={{ textAlign: "right" }}>
              <span
                  className="orange-text xs-text pointer"
                  onClick={this.closeCommentModal}
                  style={{ marginRight: 15 }}
              >
                Cancel
              </span>
                <span
                    className="orange-text xs-text pointer"
                    onClick={this.handleCommentSubmit}
                >
                Submit
              </span>
              </div>
            </div>
          </Modal>
        </div>
    );
  }
}

export default UnconfirmedDeal;
